'use client';

import React, { useState, useEffect } from 'react';
import {
  decreaseQuantity as guestDecreaseQuantity,
  getCart as guestGetCart,
  increaseQuantity as guestIncreaseQuantity,
  removeProduct as guestRemoveProduct,
} from '../../_service/GuestCartService';
import UserCartService from '../../_service/UserCartService';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/navigation';
import { createOrder } from '../../_service/OrderService';
import { setHistory } from '../../_lib/utilReducer';
import { fetchData } from '../../_lib/cartReducer'; 
import Cookies from 'js-cookie';
import { Button, Card, Typography, List, ListItem, ListItemText } from  "@shadcn/ui";

const Cart = () => {
  const dispatch = useDispatch();
  const userCartService = new UserCartService();
  const isLoggedIn = useSelector((state) => state.session.user);
  const cookie = Cookies.get("shopflow_session") ? JSON.parse(Cookies.get("shopflow_session")) : null;
  const router = useRouter();
  const products = useSelector((state) => state?.cart?.items) || [];
  const totalAmount = useSelector((state) => state?.cart?.totalAmount) || 0;
  const error = useSelector((state) => state.cart.error);
  const [loadingCheckout, setLoadingCheckout] = useState(false);

  useEffect(() => {
    const loadCart = async () => {
      if (isLoggedIn) {
        dispatch(fetchData(`/user/cart/viewCart?userId=${cookie?.user?.id}`, cookie?.token)); 
      } else {
        const guestCart = typeof window !== 'undefined' ? guestGetCart() : [];
        dispatch(fetchData({ items: guestCart, totalAmount: guestCart.reduce((acc, item) => acc + item.offerPrice * item.quantity, 0) })); 
      }
    };

    loadCart();
  }, [isLoggedIn, dispatch]);

  const handleNavigation = (productId) => {
    router.push(`/product/${productId}`);
  };

  const handleCheckout = async () => {
    setLoadingCheckout(true);
    try {
      const items = products.map((product) => ({
        productId: product.productId,
        quantity: product.quantity,
      }));
      const { order } = await createOrder(isLoggedIn.id, items);
      router.push(`/checkout/${order.id}`);
    } catch (err) {
      console.error(err);
    } finally {
      setLoadingCheckout(false);
    }
  };

  const handleIncreaseQuantity = async (productId) => {
    try {
      if (isLoggedIn) {
        await userCartService.updateCartCount(isLoggedIn.id, productId, 'increase');
      } else {
        guestIncreaseQuantity(productId);
      }
      dispatch(fetchData(isLoggedIn ? `/user/cart/viewCart?userId=${cookie?.user?.id}` : guestGetCart(), cookie?.token));
    } catch (err) {
      console.error('Failed to update item quantity:', err);
    }
  };

  const handleDecreaseQuantity = async (productId) => {
    try {
      if (isLoggedIn) {
        await userCartService.updateCartCount(isLoggedIn.id, productId, 'decrease');
      } else {
        guestDecreaseQuantity(productId);
      }
      dispatch(fetchData(isLoggedIn ? `/user/cart/viewCart?userId=${cookie?.user?.id}` : guestGetCart(), cookie?.token));
    } catch (err) {
      console.error('Failed to update item quantity:', err);
    }
  };

  const handleRemoveProduct = async (productId) => {
    try {
      if (isLoggedIn) {
        await userCartService.deleteFromCart(isLoggedIn.id, productId);
      } else {
        guestRemoveProduct(productId);
      }
      dispatch(fetchData(isLoggedIn ? `/user/cart/viewCart?userId=${cookie?.user?.id}` : guestGetCart(), cookie?.token));
    } catch (err) {
      console.error('Failed to remove product from cart:', err);
    }
  };

  return (
    <div className="bg-gray-100 min-h-screen">
      <div className="max-w-7xl mx-auto py-8 px-4 lg:px-6">
        <Typography variant="h1" className="text-3xl font-bold text-gray-800 mb-6">Shopping Cart</Typography>
        {error ? (
          <Typography className="text-center text-red-500">{error}</Typography>
        ) : products.length === 0 ? (
          <Typography className="text-center text-gray-600">Your cart is empty</Typography>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            <Card className="lg:col-span-2 bg-white rounded shadow-md p-4">
              <List>
                {products.map((product, index) => (
                  <CartTile
                    key={index}
                    handleNavigation={handleNavigation}
                    product={isLoggedIn ? product.product : product}
                    onIncreaseQuantity={handleIncreaseQuantity}
                    onDecreaseQuantity={handleDecreaseQuantity}
                    onRemoveProduct={handleRemoveProduct}
                    quantity={product?.quantity}
                  />
                ))}
              </List>
            </Card>
            <Card className="bg-white rounded shadow-md p-4 sticky top-16">
              <Typography variant="h2" className="text-lg font-semibold text-gray-800 mb-4">Price Details</Typography>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <Typography>Price</Typography>
                  <Typography>${totalAmount.toFixed(2)}</Typography>
                </div>
                <div className="flex justify-between text-green-600">
                  <Typography>Discount</Typography>
                  <Typography>- $0.00</Typography>
                </div>
                <div className="flex justify-between font-semibold">
                  <Typography>Total Amount</Typography>
                  <Typography>${totalAmount.toFixed(2)}</Typography>
                </div>
              </div>
              <Button
                onClick={isLoggedIn ? handleCheckout : () => {
                  dispatch(setHistory({ route: `/cart` }));
                  router.push("/auth");
                }}
                className={`w-full mt-4 py-2 rounded text-white ${
                  loadingCheckout ? 'bg-gray-500' : 'bg-gray-800 hover:bg-gray-900'
                }`}
                disabled={products.length === 0 || loadingCheckout}
              >
                {loadingCheckout ? 'Processing...' : 'Checkout'}
              </Button>
            </Card>
          </div>
        )}
      </div>
    </div>
  );
};

export default Cart;

function CartTile({
  handleNavigation,
  product,
  onIncreaseQuantity,
  onDecreaseQuantity,
  onRemoveProduct,
  quantity,
}) {
  const [tileLoading, setTileLoading] = useState(false);

  const handleOperation = async (operation, productId) => {
    setTileLoading(true);
    try {
      await operation(productId);
    } catch (err) {
      console.error('Operation failed:', err);
    } finally {
      setTileLoading(false);
    }
  };

  return (
    <ListItem className={`flex py-4 m-2 ${tileLoading ? 'border-2 border-double border-x-orange-500 animate-pulse' : ''}`}>
      <img className="w-16 h-16 rounded object-cover" src={product?.image} alt={product?.name} />
      <ListItemText
        primary={
          <Typography
            onClick={() => handleNavigation(product?.id)}
            className="text-gray-800 font-semibold cursor-pointer"
          >
            {product?.name}
          </Typography>
        }
        secondary={
          <>
            <Typography className="text-gray-600 text-sm">
              ${product?.offerPrice?.toFixed(2)}{' '}
              <span className="line-through text-gray-400">${product?.actualPrice?.toFixed(2)}</span>
            </Typography>
            <Typography className="text-gray-600 text-sm mt-1">{product?.description}</Typography>
            <div className="flex items-center mt-2">
              <Button
                className="px-3 py-1 bg-gray-200 text-gray-800 rounded-l hover:bg-gray-300"
                onClick={() => handleOperation(onDecreaseQuantity, product?.id)}
                disabled={tileLoading}
              >
                -
              </Button>
              <span className="px-4 py-1 bg-gray-100">{quantity}</span>
              <Button
                className="px-3 py-1 bg-gray-200 text-gray-800 rounded-r hover:bg-gray-300"
                onClick={() => handleOperation(onIncreaseQuantity, product?.id)}
                disabled={tileLoading}
              >
                +
              </Button>
            </div>
          </>
        }
      />
      <Button
        className="ml-auto text-red-500 hover:text-red-600"
        onClick={() => handleOperation(onRemoveProduct, product?.id)}
        disabled={tileLoading}
      >
        Remove
      </Button>
    </ListItem>
  );
}